}
#topic {
  background: #e1e1e1;
  padding: 10px 0;
}
#topic .panel {
  background: #fff;
}
#topic .panel_header {
  padding: 10px;
  border-bottom: 1px solid #e5e5e5;
}
#topic .panel_header .panel_header_title {
  vertical-align: middle;
  font-size: 20px;
  font-weight: bolder;
}
#topic .panel_header .put_good,
#topic .panel_header .put_top {
  vertical-align: middle;
  background: #80bd01;
  color: #fff;
  padding: 2px 4px;
  border-radius: 3px;
}
#topic .panel_header .topiclist_tab {
  vertical-align: middle;
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
}
#topic .panel_header .changes {
  font-size: 12px;
  color: #838383;
  margin-top: 14px;
}
#topic .panel_content {
  font-size: 14px;
  word-break: break-word;
  padding: 10px;
}
#topic .panel_content img {
  width: 80%;
}
#topic .panel_replies_header {
  padding: 10px;
  border-top: 15px solid #e1e1e1;
  background-color: #f6f6f6;
  border-radius: 3px 3px 0 0;
}
#topic .replies{
  padding:10px;
  border-top:1px solid #e1e1e1;
}
#topic .replies img{
    width: 30px;
    height: 30px;
    border-radius: 3px;
    vertical-align: top;
    margin-right: 8px;
}
